import React, { Fragment } from "react";
import { Link } from "@reach/router";
import styles from "./item.module.css";
const colorList = ["#e5686d", "", "#f48f18"];

export default ({
  listPicUrl,
  simpleDesc,
  name,
  retailPrice,
  colorNum,
  itemTagList,
  productPlace,
  id,
  uri
}) => {
  return (
    <li className={styles.overallWrapper}>
     <Link to={`/test/detail/item/${id}`}  >
        <div className={styles.photoWrapper}>
          <img src={listPicUrl} className={styles.photo} alt={""} />
          <div className={styles.advantage}>{simpleDesc}</div>
          {colorNum ? (
            <div className={styles.colorNum}>
              <div>{colorNum}</div>
              <div>色</div>
              <div>可</div>
              <div>选</div>
            </div>
          ) : (
            ""
          )}
          {productPlace ? (
            <div className={styles.colorNum}>{productPlace}</div>
          ) : null}
        </div>
        <div className={styles.labelList}>
          {itemTagList.map(({ name, type, tagId }) => (
            <Fragment key={tagId}>
              <div
                style={{ backgroundColor: colorList[type] }}
                className={styles.label}
              >
                {name}
              </div>
            </Fragment>
          ))}
        </div>
        <div className={styles.name}>{name}</div>
        <div className={styles.money}>¥{retailPrice}</div>
      </Link>
    </li>
  );
};



