import React, { PureComponent } from "react";
import { Link } from "@reach/router";
import styles from "./styles.module.css";
import EvaluateItem from "../../evaluateItem/item";

class evaluate extends PureComponent {
  getAjax = new XMLHttpRequest();
  state = {
    goodCmtRate: null,
    star: null
  };
  componentDidMount() {
    this.getAjax.open("GET", `/item_good_rates?id=${this.props.url}`);
    this.getAjax.send();
    this.getAjax.onload = () => {
      if (this.getAjax.readyState === 4 && this.getAjax.status === 200) {
        const { goodCmtRate, star } = JSON.parse(this.getAjax.response).data;
        this.setState({
          goodCmtRate: goodCmtRate,
          star: star
        });
      }
    };
  }
  componentWillUnmount() {
    this.getAjax.abort();
  }
  render() {
    const {
      commentCount,
      item: { comments }
    } = this.props;
    return comments.length === 0 ? null : (
      <div className={styles.wrapper}>
        <div className={styles.header}>
          <span>
            用户评价(
            {commentCount})
          </span>
          <Link
            to={`/test/detail/comment/${this.props.url}`}
            style={{ display: "flex" }}
          >
            {this.state.goodCmtRate}
            <div className={styles.iconWrapper}>
              <img
                className={styles.skipIcon}
                src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/address-right-596d39df1e.png"
                alt=""
              />
            </div>
          </Link>
        </div>
        <EvaluateItem {...comments[0]} />
      </div>
    );
  }
}

export default evaluate;



