import React, { Fragment } from "react";
import { Link } from "@reach/router";
import styles from "./styles.module.css";
export default ({ retailPrice, policyList, id,selectHistory }) =>{
  /*
  * @selectHistory :购买菜单的选择历史
  * */
    let selectContent = '';
    if(selectHistory){
   const {selected,keyMap,sum}=selectHistory;
     selected.map(
         value =>
             value !== null ? (selectContent += " " + keyMap[value].value) : ''
     );
     const haveSelect = selected.every(value=>value!==null);
     haveSelect? selectContent+=` X${sum}`:selectContent='';
 }
  return (
      <div className={styles.wrapper}>
          <div className={styles.select}>
              <span>{selectContent!==''?'已选择：'+selectContent:'请选择规格数量'}</span>
            <div className={styles.iconWrapper}>
                  <Link
                      to={`/test/detail/select/${id}`}
                      state={{ policyList: policyList, id: id }}
                  >
                      <img
                          className={styles.skipIcon}
                          src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/address-right-596d39df1e.png"
                          alt=""
                      />
                  </Link>
              </div>
          </div>
          <div className={styles.integral}>{`积分：购买最高得${retailPrice.slice(
              0,
              -1
          )}积分`}</div>
          <div className={styles.serve}>
              <div className={styles.listWrapper}>
                  <span className={styles.category}>服务:</span>
                  <ol className={styles.list}>
                      {policyList.map(({ title }, index) => (
                          <Fragment key={index}>
                              <li className={styles.item}>
                                  <div className={styles.serverContent}>{title}</div>
                              </li>
                          </Fragment>
                      ))}
                  </ol>
              </div>
              <div className={styles.iconWrapper}>
                  <Link
                      to={`/test/detail/server/${id}`}
                      state={{ policyList: policyList, id: id }}
                  >
                      <img
                          className={styles.skipIcon}
                          src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/address-right-596d39df1e.png"
                          alt=""
                      />
                  </Link>
              </div>
          </div>
      </div>
  )
};



