import React, { Component } from "react";
import AdditionalReview from './additionalReview';
import styles from "./styles.module.css";
import SwipePhoto from "../swipePhoto/swipe";
import { Subject } from "rxjs";

function timeTransition(createTime) {
  const transitionTime = new Date(createTime);
  const yearText = transitionTime.toLocaleDateString();
  const hou = transitionTime.getHours();
  const min = transitionTime.getMinutes();
  const sec = transitionTime.getSeconds();
  const hou_min_sec = hou + ":" + min + ":" + sec;
  return yearText + ' '+hou_min_sec;
}
export default class DetailItem extends Component {
  constructor() {
    super(...arguments);
    this.state = {
      show: null
    };
    this.Observable = new Subject();
  }
  componentDidMount() {
    this.Observable.subscribe(value => this.setState({ show: value }));
  }
  componentWillUnmount() {
    this.Observable.unsubscribe();
  }
  render() {
    const {
      frontUserAvatar,
      frontUserName,
      memberLevel,
      star,
      commentReplyVO,
      createTime,
      appendCommentVO,
      skuInfo,
      content,
      picList,
      id
    } = this.props;
    const startList = Array.from({ length: star }, (value, index) => ({
      id: index
    }));
    return (
      <div className={styles.wrapper}>
        <div className={styles.header}>
          <img
            className={styles.portrait}
            src={
              frontUserAvatar === null
                ? "//yanxuan.nosdn.127.net/8945ae63d940cc42406c3f67019c5cb6.png"
                : frontUserAvatar
            }
            alt=""
          />
          <span className={styles.name}>{frontUserName}</span>
          <div className={`u-icon u-icon-v${memberLevel}`} />
          {startList.map(({ id }) => (
            <React.Fragment key={id}>
              <div className={styles.starIcon} />
            </React.Fragment>
          ))}
        </div>
          <SwipePhoto
              picList={picList}
              closeSwipe={this.Observable}
              show={this.state.show}
              id={id}
          />
        <div className={styles.extraInfo}>
          <div className={styles.time}>{timeTransition(createTime)} {skuInfo.map((value, index) => (
              <React.Fragment key={index}>
                  <span>{value}</span>
              </React.Fragment>
          ))}</div>

        </div>
        <div className={styles.feel}>{content}</div>
          {picList.length!==0?<div className={styles.photoList}>
          {picList.map((value, index) => (
            <React.Fragment key={index}>
              <img
                src={value}
                alt={""}
                className={styles.photo}
                onClick={() => this.Observable.next(index)}
              />
            </React.Fragment>
          ))}
        </div>:null}
        {commentReplyVO !== null ? (
          <div className={styles.reply}>
            <span className={styles.replyTitle}>小选回复：</span>
            {commentReplyVO.replyContent}
          </div>
        ) : null}
          {appendCommentVO?<AdditionalReview {...appendCommentVO}/>:null}
      </div>
    );
  }
}



