import React, { Fragment, PureComponent } from "react";
import Swiper from "swiper/dist/js/swiper.min";
import styles from "./styles.module.css";

export default class Swipe extends PureComponent {
  constructor() {
    super(...arguments);
    this.swiper = null;
  }
  componentDidMount() {
    this.swiper = new Swiper(
      `${
        this.props.id !== undefined ? "#" + this.props.id : ".swiper-container"
      }`,
      { width: window.innerWidth }
    );
    this.props.closeSwipe.subscribe(
      value => (value !== null ? this.swiper.slideTo(value,0) : null)
    );
  }
  componentWillUnmount() {
    Array.isArray(this.swiper)
      ? this.swiper.map(value => value.destroy())
      : this.swiper.destroy();
  }
  render() {
    const { picList, show, closeSwipe, id = "" } = this.props;
    return (
      <div
        onClick={() => closeSwipe.next(null)}
        style={{ display: show !== null ? "block" : "none" }}
      >
        <div className={`swiper-container ${styles.wrapper}`} id={id}>
          <div className={`swiper-wrapper ${styles.list}`}>
            {picList.map((value, index) => (
              <Fragment key={index}>
                <div className={`swiper-slide `}>
                  <div className={styles.photo}>
                    <img src={value} alt="" />
                  </div>
                </div>
              </Fragment>
            ))}
          </div>
        </div>
      </div>
    );
  }
}



