import React, { Component } from "react";
import styles from "./index.module.css";
import { fromEvent } from "rxjs";
import { map } from "rxjs/operators";

export default class ReturnTop extends Component {
  constructor() {
    super();
    this.state = {
      show: false
    };
    this.moveAnimation = this.moveAnimation.bind(this);
    this.getObservable = fromEvent(window, "scroll");
    this.intenObser = null;
  }
  componentDidMount() {
    this.intenObser = this.getObservable
      .pipe(map(() => document.documentElement.scrollTop > 1000))
      .subscribe(value =>
        this.setState({
          show: value
        })
      );
  }
  componentWillUnmount() {
    this.intenObser.unsubscribe();
  }
  moveAnimation() {
    function returnAnimation(destination, origin, callback) {
      const nextDistance = function() {
        origin = origin / 2;
        callback(origin);
        if (origin < 1) {
          return false;
        }
        window.requestAnimationFrame(nextDistance);
      };
      nextDistance();
    }
    returnAnimation(0, window.pageYOffset, function(value) {
      document.documentElement.scrollTop = value;
    });
  }
  render() {
    return (
      <div
        className={styles.button}
        onClick={this.moveAnimation}
        style={{ display: this.state.show ? "block" : "none" }}
      />
    );
  }
}



