import React, { Component, PureComponent } from "react";
import Header from "header/header";
import ReturnTop from "returnTop/index";
import Loading from "loading/lottie";
import Carousel from "./carouselWrapper/carousel";
import CharacteristicList from "./characteristicList/characteristicList";
import Intro from "./intro/intro";
import Select from "./select/list";
import Evaluate from "./evaluate/evaluate";
import Parameter from "./parameter/parameter";
import QualityTesting from "./qualityTesting/popUp";
import Issue from "./issue/issue";
import HotList from "./hotList/hotList";
import styles from "./view.module.css";

class Detail extends PureComponent {
  constructor() {
    super(...arguments);
    const { getHot, items } = this.props;
    if (this.props.getHot) {
      this.state = {
        getHot: getHot,
        items: items
      };
    } else {
      this.state = {
        getHot: false,
        items: null
      };
    }
      this.getAjax = new XMLHttpRequest();
  }
  componentDidMount() {
    if (!this.state.getHot) {
      this.getAjax.open("GET", `/host_port?item=${this.props.url}`);
      this.getAjax.onload = () => {
        if (this.getAjax.readyState === 4 && this.getAjax.status === 200) {
          this.setState({
            getHot: true,
            items: JSON.parse(this.getAjax.response).data.items
          });
        }
      };
      this.getAjax.send();
    }
  }
  componentWillUnmount() {
      this.getAjax.abort();
      if (this.props.CacheObservable.observers !== null) {
      //缓存当前数据
      this.props.CacheObservable.next({
        type: "cacheState",
        state: Object.assign({}, this.props, this.state)
      });
    }
  }
  render() {
    const { item, policyList, commentCount } = this.props.CacheState;
    return (
      <div>
        <div style={{ backgroundColor: "#f7f7f7", paddingBottom: "1.6rem" }}>
          <Header />
          <Carousel list={item.itemDetail} primaryPicUrl={item.primaryPicUrl} />
          <CharacteristicList list={item.characteristicList} />
          <Intro {...item} />
          <Select
            selectHistory={this.props.selectHistory}
            retailPrice={item.retailPrice + ""}
            policyList={policyList}
            id={item.id}
          />
          <Evaluate
            commentCount={commentCount}
            item={item}
            url={this.props.url}
          />
          <Parameter attrList={item.attrList} />
          <div
            dangerouslySetInnerHTML={{ __html: item.itemDetail.detailHtml }}
            className={styles.particulars}
          />
          {item.reportPicList !== null ? (
            <QualityTesting reportPicList={item.reportPicList} id={item.id} />
          ) : null}
          <Issue issueList={item.issueList} />
          {this.state.getHot ? <HotList listState={this.state.items} /> : null}
          <ReturnTop />
        </div>
      </div>
    );
  }
}

export default class ListRender extends Component {
  constructor() {
    super(...arguments);

    this.props.CacheObservable.next({
      thisId: this.props.url,
      historyId: this.props.CacheId,
      requestOptions: {
        method: this.props.requestOptions.method,
        url: this.props.requestOptions.url + this.props.url
      }
    });
  }
  componentDidUpdate(prevProps) {
    if (this.props.url !== prevProps.url) {
        this.props.CacheObservable.next({
            thisId: this.props.url,
            historyId: this.props.CacheId,
            requestOptions: {
                method: this.props.requestOptions.method,
                url: this.props.requestOptions.url + this.props.url
            }
        });
        document.documentElement.scrollTop = 0;
    }
  }
  render() {
    return this.props.CacheState ? (
      <Detail {...this.props} />
    ) : (
      <Loading stop={Object.is(this.props.CacheState, null)} />
    );
  }
}



