import React, { Component } from "react";
import { Carousel } from "antd-mobile";
import styles from "./style.module.css";

class CarouselWrapper extends Component {
  constructor() {
    super(...arguments);
    this.state = {
      carouselNum: 1
    };
    this.photoSkip = this.photoSkip.bind(this);
  }
  photoSkip(num) {
    this.setState({
      carouselNum: num
    });
  }
  render() {
    const { picUrl1, picUrl2, picUrl3, picUrl4 } = this.props.list;
    const { primaryPicUrl } = this.props;
    return (
      <div className={styles.wrapper}>
        <Carousel
          className={styles.carousel}
          dots={false}
          autoplay={true}
          infinite={true}
          slideWidth={1}
          autoplayInterval={5000}
          afterChange={to => this.photoSkip(to)}
        >
          <img src={primaryPicUrl} alt="" className={styles.photo} />
          <img src={picUrl1} alt="" className={styles.photo} />
          <img src={picUrl2} alt="" className={styles.photo} />
          <img src={picUrl3} alt="" className={styles.photo} />
          <img src={picUrl4} alt="" className={styles.photo} />
        </Carousel>
        <div className={styles.carouselNum}>
          {this.state.carouselNum + 1}
          /5
        </div>
      </div>
    );
  }
}

export default CarouselWrapper;



