import React, { PureComponent } from "react";
import styles from "./style.module.css";
import Title from "../categoryTitle/title";
import SwipePhoto from "../../swipePhoto/swipe";
import { Subject } from "rxjs";

export default class qualityTesting extends PureComponent {
  constructor() {
    super(...arguments);
    this.state = {
      show: null
    };
    this.Observable = new Subject();
  }
  componentDidMount() {
    this.Observable.subscribe(value => this.setState({ show: value }));
  }
  componentWillUnmount() {
    this.Observable.unsubscribe();
  }
  render() {
    const { reportPicList, id } = this.props;
    return (
      <div className={styles.wrapper}>
        <Title title={"质检报告"} />
        <div className={styles.report}>
          <div className={styles.icon} />
          <img
            src={reportPicList[0]}
            alt=""
            className={styles.picture}
            onClick={() => this.Observable.next(0)}
          />
        </div>
        <SwipePhoto
          id={id}
          picList={reportPicList}
          closeSwipe={this.Observable}
          show={this.state.show}
        />
      </div>
    );
  }
}



