import React, { Fragment } from "react";
import styles from "./styles.module.css";

export default ({ attrList }) => (
  <div className={styles.wrappeer}>
    <div className={styles.header}>商品参数</div>
    {attrList.map(({ attrName, attrValue }, index) => (
      <Fragment key={index}>
        <div className={styles.item}>
          <div className={styles.name}>{attrName}</div>
          <div className={styles.parameter}>{attrValue}</div>
        </div>
      </Fragment>
    ))}
  </div>
);



