import React, { Fragment } from "react";
import styles from "./style.module.css";

export default ({ list }) => {
  return (
    <div className={styles.list}>
      {list.map(({ picUrl, simpleDescList }, index) => (
        <Fragment key={index}>
          <div className={styles.item}>
            <img src={picUrl} alt="" className={styles.photo} />
            <div className={styles.text}>
              {simpleDescList[0]}
              <br />
              {simpleDescList[1]}
            </div>
          </div>
        </Fragment>
      ))}
    </div>
  );
};



